.muscle-selector {
  text-align: center;
  padding: 20px;
}

.svg-container {
  margin: 20px auto;
  width: 300px;
  height: 400px;
  border: 1px solid #ccc;
  position: relative;
}

.muscle-map rect {
  transition: fill 0.3s ease;
}

.equipment-selector {
  margin-top: 20px;
}

.exercise-list {
  margin-top: 20px;
}

.exercise-list ul {
  list-style: none;
  padding: 0;
}

.exercise-list li {
  padding: 10px;
  border: 1px solid #ddd;
  margin: 5px 0;
  border-radius: 5px;
}

.muscle-map path {
  transition: fill 0.3s ease;
}

.muscle-map path:hover {
  fill: #0056b3;
}
