body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  color: #333;
  overflow-x: hidden;
}

h1,
h2,
h3 {
  font-weight: bold;
  margin-bottom: 15px;
}

header {
  background-color: #282c34;
  color: white;
  padding: 1rem;
  text-align: center;
}

nav {
  background-color: #282c34;
  /* Темный минималистичный фон */
  padding: 10px 20px;
  /* Увеличенные отступы для более воздушного вида */
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  /* Легкая тень для разделения с основным контентом */
  position: sticky;
  /* Фиксированный верх при прокрутке */
  top: 0;
  z-index: 1000;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  /* Равномерное распределение вкладок */
  align-items: center;
  margin: 0;
  padding: 0;
  gap: 15px;
  /* Промежутки между элементами */
}

nav ul li {
  padding: 8px 15px;
  /* Удобные зоны для клика */
  border-radius: 6px;
  /* Скругленные углы для современного дизайна */
  color: white;
  font-size: 16px;
  transition: background-color 0.3s, transform 0.3s;
  cursor: pointer;
}

nav ul li:hover {
  background-color: #444;
  /* Темно-серый цвет при наведении */
  transform: scale(1.05);
  /* Легкий масштаб для интерактивности */
}

nav ul li.active {
  background-color: #007BFF;
  /* Синий для активной вкладки */
  font-weight: bold;
}

nav ul li a {
  color: inherit;
  /* Наследование цвета текста */
  text-decoration: none;
  /* Удаление подчеркивания */
  display: block;
}

/* Адаптивный дизайн для мобильных устройств */
@media (max-width: 768px) {
  nav ul {
    flex-direction: column;
    /* Вкладки в вертикальном виде на малых экранах */
    align-items: flex-start;
    gap: 10px;
  }

  nav ul li {
    width: 100%;
    /* Растягивание вкладок на всю ширину */
    text-align: left;
  }
}



.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.workout-plan,
.calorie-tracker,
.diary,
.article-section {
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
  padding: 1.5rem;
  opacity: 0;
  transform: translateY(50px);
  animation: slideUp 1s ease-out forwards;
}

.workout-plan input,
.calorie-tracker input,
.diary input {
  margin: 0.5rem 0;
  padding: 0.5rem;
  width: calc(100% - 1rem);
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.workout-plan button,
.calorie-tracker button,
.diary button {
  margin: 0.5rem 0;
  padding: 0.5rem 1rem;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.workout-plan button:hover,
.calorie-tracker button:hover,
.diary button:hover {
  background-color: #0056b3;
}

.workout-plan ul,
.diary ul {
  list-style: none;
  padding: 0;
}

.workout-plan li,
.diary li {
  margin: 0.5rem 0;
  padding: 0.5rem;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0;
  transform: translateY(50px);
  animation: slideUp 1s ease-out 0.5s forwards;
}

.article-section {
  text-align: left;
  line-height: 1.8;
  max-width: 1000px;
  margin: 30px auto;
  padding: 20px;
}

.article-section h2 {
  color: #007BFF;
}

.article-section p {
  margin: 0.5rem 0;
  font-size: 18px;
  line-height: 1.8;
  color: #7f8c8d;
}

.article-section i {
  font-style: italic;
  font-size: 14px;
  color: #2c3e50;
  display: block;
  margin-top: 20px;
}

.calorie-tracker input {
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 18px;
}

.calorie-tracker button {
  width: 100%;
  padding: 12px;
  background-color: #2980b9;
  color: white;
  font-size: 18px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 10px;
}

.calorie-tracker button:hover {
  background-color: #3498db;
}

.workout-plan input {
  width: calc(33.333% - 10px);
  margin-bottom: 15px;
  margin-right: 10px;
}

.workout-plan ul {
  padding: 0;
}

.workout-plan li {
  flex-direction: column;
  align-items: flex-start;
}

.diary h3 {
  margin-top: 20px;
  font-size: 22px;
  color: #2980b9;
}

.diary button {
  background-color: #e74c3c;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 10px 15px;
}

.diary button:hover {
  background-color: #c0392b;
}

/* Slide-Up Animation */
@keyframes slideUp {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .container {
    padding: 1rem;
  }

  .workout-plan input,
  .calorie-tracker input,
  .diary input {
    width: 100%;
    margin-right: 0;
  }

  .workout-plan button,
  .calorie-tracker button,
  .diary button {
    width: 100%;
  }

  .workout-plan li {
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  header {
    text-align: left;
    padding: 0.5rem 1rem;
  }

  nav ul {
    flex-direction: column;
    align-items: flex-start;
    padding: 0 1rem;
  }

  nav ul li {
    margin: 0.5rem 0;
  }
}

.article-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
}

.article-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  height: 400px;
  /* Фиксированная высота карточек */
  background-color: #fff;
  transition: transform 0.3s, box-shadow 0.3s;
}

.article-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.image-wrapper {
  width: 100%;
  height: 200px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f4f4f4;
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  user-select: none;
}

.card-content {
  padding: 15px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: left;
}

.card-content h3 {
  font-size: 1.2rem;
  margin: 0 0 10px;
  line-height: 1.4;
  color: #333;
}

.card-content p {
  font-size: 0.9rem;
  color: #666;
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.article-image {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
  margin-bottom: 20px;
}

.centered {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
